@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
body{margin:0;font-family:"Roboto", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}button{border:none}ul{padding:0;list-style:none}@media (min-width: 800px){.body-layout{display:flex}}

.persons-list{margin:8px 0;max-height:160px;overflow:scroll;border:1px solid #f7f7f7}@media (min-width: 800px){.persons-list{max-height:400px}}.error{color:red;font-size:20px;text-align:center;margin:20px 0}.persons-list__person{padding:4px 8px;cursor:pointer}.persons-list__person:nth-child(even){background-color:#f7f7f7}.persons-list__person:hover{font-weight:bold}.persons-list__person.active{font-weight:bold}.actions{display:flex;justify-content:center}.actions__modify-list{margin:0 8px;border-radius:4px;text-transform:uppercase;padding:14px 16px 14px 16px !important;font-size:14px;line-height:20px;text-align:center;color:#4a4a4a;font-weight:400;background-color:white;box-shadow:0px 0px 2px rgba(55,71,79,0.2),0px 8px 12px rgba(0,0,0,0.1)}.persons-list-container{margin:8px 0}

.invalid{border:1px solid red}

